<template>
    <div>
        <h1>Notas 2020</h1>
        <label>Buscar:</label>
        <input v-model="nombre" />
        <div>
            <div id="alumnos" v-if="alumnos">
                <table id="defensa">
                    <thead>
                        <tr>
                            <th>Nombre</th>
                            <th>Apellido</th>
                            <th>Corrige</th>
                            <th>GitHub</th>
                            <th colspan="2">Proyecto 1</th>
                            <th colspan="2">Proyecto 2</th>
                            <th colspan="2">Proyecto 3</th>
                            <th>Resultado</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="alumno in alumnosF" :key="alumno.Nombre">
                            <td>{{ alumno.Nombre }}</td>
                            <td>{{ alumno.Apellido }}</td>
                            <td>{{ alumno.Corrige }}</td>
                            <td>
                                <a
                                    target="_blank"
                                    v-bind:href="`https://github.com/${alumno.GitHub}`"
                                    >GitHub</a
                                >
                            </td>
                            <td>{{ alumno['Resultado Proyecto 1'] }}</td>
                            <td>{{ alumno['Nota Proyecto 1'] }}</td>
                            <td>{{ alumno['Resultado Proyecto 2'] }}</td>
                            <td>{{ alumno['Nota Proyecto 2'] }}</td>
                            <td>{{ alumno['Resultado Proyecto 3'] }}</td>
                            <td>{{ alumno['Nota Proyecto 3'] }}</td>
                            <td>{{ alumno['Estado'] }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <h2 v-if="error">Ocurrió un error: {{ error }}</h2>
        </div>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            nombre: '',
            alumnos: null,
            error: null,
        };
    },
    mounted() {
        fetch('alumnos.json')
            .then((res) => res.json())
            .then(
                (result) => {
                    this.alumnos = result;
                },
                (error) => {
                    this.error = error;
                }
            );
    },
    computed: {
        alumnosF () {
            const f = this.nombre.toUpperCase();
            return this.alumnos.slice()
                .sort(function(a, b) {
                    var nameA = a.Apellido.toUpperCase();
                    var nameB = b.Apellido.toUpperCase();
                    if (nameA < nameB) {
                        return -1;
                    }
                    if (nameA > nameB) {
                        return 1;
                    }

                    // names must be equal
                    return 0;
                })
                .filter(
                    (alumno) =>
                        alumno.Corrige.toUpperCase().includes(f) ||
                        alumno.Nombre.toUpperCase().includes(f) ||
                        alumno.Estado.toUpperCase().includes(f) ||
                        alumno.Apellido.toUpperCase().includes(f)
                );
        },

    },
};
</script>
